/* 0311-0540. ADDITEM

10. cart item  item   find()

15.  cart   ,  

  + 

payload  item... spread ... [],  qty:1 


20. cart  item  , 

20-10. cart item id === payload  item id  - map()



30.    cart   , 

cart item... spread ... [] , qty +1 



30-10.   cart  ,     return  ()
*/

const cart = [];

const reducer_handleCart = (state = cart, action) => {
  
  switch (action.type) {
    case "ADDITEM":
      // check if product is already existing

      const exist = state.find((x) => x.id === action.payload.id);

      if (exist) {          //// 0311-0540-30
        // incerase qty
        return state.map((x) =>
          x.id === action.payload.id 

          ? { ...x, 
            qty: x.qty + 1 
            } 

          : x
        );

      } else {    // 0311-0540-20
        return [
          ...state,
          {
            ...action.payload,
            qty: 1,
          },
        ];
      }

      break;


      /* 
      0311-0600. DELITEM
      
        10. cart item  item   find()

        20.find  cart item qty === 1 ,   

        -->      filter return

        30. find  cart item qty !== 1 , 

        cart item ===  item ...map()    

         .qty -1
      */
    case "DELITEM":
        const exist_del = state.find((x) => x.id === action.payload.id);

        if (exist_del.qty === 1 ) {
            return state.filter((x)=>x.id !== exist_del.id);

        }else{
            return state.map((x)=> x.id === action.payload.id 
            
            ? {
              ...x,
              qty: x.qty-1
              } 
            
            : x );
        }

        break;



        // js0310-0440 clearItem redeucer_handleCart.js , redux/index.js, Cartjs.jsx

        /* 0311-0630. clearItem
             id !==  id... : id   return = id 
        */
        
      case "clearItem":

        return state = state.filter((x)=>{
          return x.id !== action.payload.id

          cart=[];
        })
        
        break;

    default:
        return state;
      break;
  }
};

export default reducer_handleCart;